import React, {Component} from 'react';
import {Table, Divider, Card } from 'antd';
import LineChart from './LineChart';
import {postAction} from "../../../../axios";
import MainCard from '../../../../common/components/MainCard';
import {hostUrl} from "../../../../env";
import ReactDOM from "react-dom";
import NoData from '@/common/components/NoData/NoDataComponent';


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '6%',
},{
    title: '时间',
    dataIndex: 'time',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '失联预警(条)',
    dataIndex: 'alarmMiss'
}, {
    title: '网络预警(条)',
    dataIndex: 'alarmNetwork'
}, {
    title: '挂科预警(条)',
    dataIndex: 'alarmFail'
}, {
    title: '消费预警(条)',
    dataIndex: 'alarmConsume'
}, {
    title: '夜归预警(条)',
    dataIndex: 'alarmBack'
}, {
    title: '逃课预警(条)',
    dataIndex: 'alarmCourse'
}, {
    title: '红色预警(条)',
    dataIndex: 'red'
}, {
    title: '橙色预警(条)',
    dataIndex: 'orange'
}, {
    title: '黄色预警(条)',
    dataIndex: 'yellow'
}, {
    title: '蓝色预警(条)',
    dataIndex: 'blue'
}];


export default class QingKuangFenXi extends Component {

    constructor(props){
        super(props);
        this.state = {
            lastSixMonthsCategory: [],
            tab: '1',
            chartIns: undefined,
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            total: 0
        }
    }

    getData() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filter;
        let data = {
            year: curYear,
            collegeCode: curCollege,
            majorCode: curMajor,
            grade: curGrade,
            classCode: curClass
        };
        // 近一年预警类别分布趋势
        postAction("/bigdata/alarmTrigger/getAlarmByYear", data).then(
            (res) => {
                if (res.success) {

                    const pageInfo = res.obj;
                    let _lastSixMonthsCategory = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {
                            _lastSixMonthsCategory.push({
                                createMonth: item.createMonth,
                                '失联预警': item.alarmMiss,
                                '网络预警': item.alarmNetwork,
                                '挂科预警': item.alarmFail,
                                '消费预警': item.alarmConsume,
                                '夜归预警': item.alarmBack,
                                '逃课预警': item.alarmCourse,
                                '红色预警': item.red,
                                '橙色预警': item.orange,
                                '黄色预警': item.yellow,
                                '蓝色预警': item.blue
                            })

                            _tableData.push({
                                key: index + 1,
                                sortIndex: index + 1,
                                time:item.createMonth,
                                alarmMiss: item.alarmMiss ? item.alarmMiss : 0,
                                alarmNetwork: item.alarmNetwork ? item.alarmNetwork : 0,
                                alarmFail: item.alarmFail ? item.alarmFail : 0,
                                alarmConsume: item.alarmConsume ? item.alarmConsume : 0,
                                alarmBack: item.alarmBack ? item.alarmBack : 0,
                                alarmCourse: item.alarmCourse ? item.alarmCourse : 0,
                                red: item.red ? item.red : 0,
                                orange: item.orange ? item.orange : 0,
                                yellow: item.yellow ? item.yellow : 0,
                                blue: item.blue ? item.blue : 0
                            })
                        }
                    )

                    this.setState({
                        lastSixMonthsCategory: _lastSixMonthsCategory,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: pageInfo.length, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        },
                    })
                } else {
                    this.setState({
                        lastSixMonthsCategory:[],
                        tableData:[],
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        },
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear, curGrade, curCollege, curMajor, curClass} = this.props.filter;

        window.location.href = `${hostUrl}/bigdata/alarmTrigger/exportAlarm?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'fenxi-xls')
            }
        })
    }

    render () {

        if (this.state.lastSixMonthsCategory && this.state.lastSixMonthsCategory[0]) {
            return (
                <MainCard
                    className="main-card"
                    title={<div className="common-title">预警情况分析
                        <span className="unit">单位：条</span>
                        <div className="filter-text" style={{top:20,left:0}}>{this.props.filter.curYearName}
                            <Divider type="vertical" />{this.props.filter.curCollegeName}
                            <Divider type="vertical" />{this.props.filter.curMajorName}
                            <Divider type="vertical" />{this.props.filter.curGradeName}
                            <Divider type="vertical" />{this.props.filter.curClassName}
                        </div>
                    </div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    downloadChart={{
                        chartIns: this.state.chartIns,
                        title: "预警情况分析"
                    }}
                    detailCallback={this.getDetail.bind(this)}
                    tableToExcel={{
                        table: 'fenxi-xls',
                        filename: '预警情况分析excel下载'
                    }}
                >
                    {
                        this.state.tab === '1'
                            ?
                            <LineChart
                                data={this.state.lastSixMonthsCategory}
                                colors={["#5cb9ef","#8a7bd4","#435188","#fbd437",'#2fc25b','#13C2C2','#f04864','#f5a623','#facc14','#1890ff']}
                                onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            />
                            :
                            <React.Fragment>
                                <Table
                                    dataSource={this.state.tableData}
                                    columns={columns}
                                    bordered
                                    size="middle"
                                    style={{height:"350px"}}
                                    pagination={this.state.pagination}
                                    onChange={this.handleTableChange}
                                />

                                <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                            </React.Fragment>
                    }
                </MainCard>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title">预警情况分析
                        <span className="unit">单位：条</span></div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}

                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            )
        }

    }

    componentDidMount(){
        this.getData();
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


