import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import PieChart from './PieChart';
import {postAction} from "../../../../axios";
import MainCard from '../../../../common/components/MainCard';
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '预警处理结果',
    dataIndex: 'status',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '预警处理结果（条数）',
    dataIndex: 'number'
}];

export default class ChuLiJieGuo extends Component {

    constructor(props){
        super(props);
        this.state = {
            resultData: [],
            tab: '1',
            chartIns: undefined,
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
    }

    getData() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filter;
        let data = {
            year: curYear,
            collegeCode: curCollege,
            majorCode: curMajor,
            grade: curGrade,
            classCode: curClass
        };
        // 预警处理结果
        postAction("/bigdata/alarmTrigger/getAlarmHandlePercent",data).then(
            (res) => {
                if (res.success) {

                    const pageInfo = res.obj;
                    let _resultData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {
                            _resultData.push({
                                name: ['未处理','已处理'][item.status],
                                amount: item.amount
                            })

                            _tableData.push({
                                key: index + 1,
                                sortIndex: index + 1,
                                status:['未处理','已处理'][item.status],
                                number: item.amount
                            })
                        }
                    )
                    this.setState({
                        resultData: _resultData,
                        tableData: _tableData,
                        pagination:{
                            ...this.state.pagination,
                            total:pageInfo.length,
                        }
                    })
                } else {
                    this.setState({
                        resultData:[],
                        tableData:[],
                        pagination:{
                            ...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear, curGrade, curCollege, curMajor, curClass} = this.props.filter;

        window.location.href = `${hostUrl}/bigdata/alarmTrigger/exportAlarm?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'yujing-xls')
            }
        })
    }

    render () {

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">预警处理结果
                    <span className="unit">单位：条</span>
                    <div className="filter-text" style={{top:20,left:0}}>{this.props.filter.curYearName}
                        <Divider type="vertical" />{this.props.filter.curCollegeName}
                        <Divider type="vertical" />{this.props.filter.curMajorName}
                        <Divider type="vertical" />{this.props.filter.curGradeName}
                        <Divider type="vertical" />{this.props.filter.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "预警处理结果"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: 'yujing-xls',
                    filename: '预警处理结果excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                <PieChart
                    data={this.state.resultData}
                    position={'right-center'}
                    padding={[20, 120, 40, 20]}
                    offsetY={-20}
                    onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="middle"
                                style={{height:"350px"}}
                                pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount(){
        this.getData();
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


