import React, { Component } from 'react';
import { Chart, Geom, Axis, Tooltip, Coord, Legend } from "bizcharts"
import { DataSet } from '@antv/data-set';
import NoData from '../../../../common/components/NoData/NoDataComponent';
import Slider from "bizcharts-plugin-slider";

let ds;
let initEnd = 0;

// 柱状图
class BarChart extends Component {
    // 第一次加载完触发事件
    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }
    
    componentWillReceiveProps(nextProps){
		let data = nextProps.data;
		if(data && data[0]) {
			initEnd = 7;
			setTimeout(
				() => {
					ds.setState("start", 0);
					ds.setState("end", initEnd);
				}
			)
		}
	}
    
    render() {
        const height = this.props.height || 350; 
        let fields = [],
			data = this.props.data,
			nameKey;
			
        if (data && data[0]) {
        	
            nameKey = Object.keys(data[0])[0];
            Object.keys(data[0]).forEach((item,index)=>{
                if(index!==0){
                    fields.push(item);
                }
            })
            
            // 分组图
	        ds = new DataSet({
	        	state:{
					start: 0,
      				end: data.length-1
				}
	        }); // dateset数据集方法
	        const dv = ds.createView().source(data);// 数据转换
	        // 初始化参数
	        dv.source(data).transform({
	            type: 'fold',
	            fields: fields.length>0?fields:['null'],
	            key: 'type',
	            value: 'value'
	        }).transform({
				type: 'filter',
				callback: obj => {
			        const name = obj.name;
					let curIndex;
					this.props.data.forEach(
						(item, index) => {
							if (item.name === name) {
								curIndex = index
							}
						}
					)
					return curIndex <= ds.state.end && curIndex >= ds.state.start;
			    }
			})
	        
	        return (
	        	<React.Fragment>
		            <Chart 
		            	height={height -32} 
		            	data={dv} 
		            	scale={this.props.scales} 
		            	padding={this.props.padding} 
		            	forceFit
	                    onGetG2Instance={(ev)=>this.onFirstLoad(ev)}
		            >
		                {this.props.transpose?<Coord transpose />:null}
		                <Axis name = {nameKey} />
		                <Axis name="value" />
		                {this.props.noLegend?null:<Legend marker={'circle'}/>}
		                <Tooltip crosshairs={{type : "y"}}/>
		                <Geom
		                    type={this.props.geomType || 'interval'}
		                    position={nameKey+"*value"}
		                    size={this.props.geomSize}
		                    color={['type',this.props.colors]}
		                    shape={this.props.shape}
		                    adjust={this.props.adjust || [{type: 'dodge',marginRatio: 1/32}]} tooltip={this.props.geomTooltip} 
		                />
		                {this.props.children}
		            </Chart>
		            <Slider
						width="auto"
						height={26}
						start={0}
						end={initEnd}
						padding={[20,80,95,80]}
						xAxis={nameKey}
						yAxis="value"
						onChange = {(obj) => {
							const { startValue, endValue } = obj;
							let _start, _end;
							this.props.data.forEach(
								(item, index) => {
									if(item.name === startValue) {
										_start = index
									}
									if(item.name === endValue) {
										_end = index
									}
								}
							)
							ds.setState("start", _start);
							ds.setState("end", _end);
						}}
						data={dv}
					/>
		        </React.Fragment>
	        )
            
        } else {
            // 无数据时
            return (<NoData height={height} data={'nodata'}/>);
        }
    }
}

export default BarChart;


