import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import BarChart from './BarChart';
import {postAction} from "../../../../axios";
import MainCard from '../../../../common/components/MainCard';
import {hostUrl} from "../../../../env";
import ReactDOM from "react-dom";


export default class BuTongXueYuan extends Component {

    constructor(props){
        super(props);
        this.state = {
            collegeAnalysisData: [],
            tab: '1',
            chartIns: undefined,
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            columns : [{
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院',
                dataIndex: 'college',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }, {
                title: '失联预警(条)',
                dataIndex: 'alarmMiss'
            }, {
                title: '网络预警(条)',
                dataIndex: 'alarmNetwork'
            }, {
                title: '挂科预警(条)',
                dataIndex: 'alarmFail'
            }, {
                title: '消费预警(条)',
                dataIndex: 'alarmConsume'
            }, {
                title: '夜归预警(条)',
                dataIndex: 'alarmBack'
            }, {
                title: '逃课预警(条)',
                dataIndex: 'alarmCourse'
            }]
        }
    }

    getData() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filter;
        let data = {
            year: curYear,
            collegeCode: curCollege,
            majorCode: curMajor,
            grade: curGrade,
            classCode: curClass
        };
        // 不同学院预警分析
        postAction("/bigdata/alarmTrigger/getCollegeAlarmType", data).then(
            (res) => {
                if (res.success) {

                    const pageInfo = res.obj;
                    let _collegeAnalysisData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {
                            _collegeAnalysisData.push({
                                name: item.collegeName,
                                '失联预警': item.alarmMiss ? item.alarmMiss : 0,
                                '网络预警': item.alarmNetwork ? item.alarmNetwork : 0,
                                '挂科预警': item.alarmFail ? item.alarmFail : 0,
                                '消费预警': item.alarmConsume ? item.alarmConsume : 0,
                                '夜归预警': item.alarmBack ? item.alarmBack : 0,
                                '逃课预警': item.alarmCourse ? item.alarmCourse : 0
                            })

                            _tableData.push({
                                key: index + 1,
                                sortIndex: index + 1,
                                college: item.collegeName,
                                alarmMiss: item.alarmMiss ? item.alarmMiss : 0,
                                alarmNetwork: item.alarmNetwork ? item.alarmNetwork : 0,
                                alarmFail: item.alarmFail ? item.alarmFail : 0,
                                alarmConsume: item.alarmConsume ? item.alarmConsume : 0,
                                alarmBack: item.alarmBack ? item.alarmBack : 0,
                                alarmCourse: item.alarmCourse ? item.alarmCourse : 0,
                            })
                        }
                    )

                    this.setState({
                        collegeAnalysisData: _collegeAnalysisData,
                        tableData: _tableData,
                        pagination:{
                            ...this.state.pagination,
                            total:pageInfo.length,
                        }
                    })
                } else {
                    this.setState({
                        collegeAnalysisData:[],
                        tableData:[],
                        pagination:{
                            ...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear, curGrade, curCollege, curMajor, curClass} = this.props.filter;

        window.location.href = `${hostUrl}/bigdata/alarmTrigger/exportAlarm?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'butong-xls')
            }
        })
    }

    render () {

        return (

            <MainCard
                className="main-card"
                title={<div className="common-title">不同学院预警分析
                    <span className="unit">单位：条</span>
                    <div className="filter-text" style={{top:20,left:0}}>{this.props.filter.curYearName}
                        <Divider type="vertical" />{this.props.filter.curCollegeName}
                        <Divider type="vertical" />{this.props.filter.curMajorName}
                        <Divider type="vertical" />{this.props.filter.curGradeName}
                        <Divider type="vertical" />{this.props.filter.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "不同学院预警分析"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: 'butong-xls',
                    filename: '不同学院预警分析excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?

                <BarChart
                    geomType="intervalStack"
                    data={this.state.collegeAnalysisData}
                    colors={['#f04864','#F5A623','#13C2C2','#1890ff','#2fc25b','#facc14']}
                    padding={[5,50,80,50]}
                    onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={this.state.columns}
                                bordered
                                size="middle"
                                style={{height:"350px"}}
                                pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount(){
        this.getData();
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院"){
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    },{
                        title: "学院",
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '失联预警(条)',
                        dataIndex: 'alarmMiss'
                    }, {
                        title: '网络预警(条)',
                        dataIndex: 'alarmNetwork'
                    }, {
                        title: '挂科预警(条)',
                        dataIndex: 'alarmFail'
                    }, {
                        title: '消费预警(条)',
                        dataIndex: 'alarmConsume'
                    }, {
                        title: '夜归预警(条)',
                        dataIndex: 'alarmBack'
                    }, {
                        title: '逃课预警(条)',
                        dataIndex: 'alarmCourse'
                    }]
                })
            } else {
                this.setState({
                    columns: [{
                        title: '序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    }, {
                        title: "专业",
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '失联预警(条)',
                        dataIndex: 'alarmMiss'
                    }, {
                        title: '网络预警(条)',
                        dataIndex: 'alarmNetwork'
                    }, {
                        title: '挂科预警(条)',
                        dataIndex: 'alarmFail'
                    }, {
                        title: '消费预警(条)',
                        dataIndex: 'alarmConsume'
                    }, {
                        title: '夜归预警(条)',
                        dataIndex: 'alarmBack'
                    }, {
                        title: '逃课预警(条)',
                        dataIndex: 'alarmCourse'
                    }]
                })

                if (nextProps.filterName.curMajorName !== "全部专业") {
                    this.setState({
                        columns: [{
                            title: '序号',
                            dataIndex: 'sortIndex',
                            width: '10%',
                        }, {
                            title: "班级",
                            dataIndex: 'college',
                            render: (text) => {
                                return (
                                    <span title={text}>{text}</span>
                                )
                            }
                        },{
                            title: '失联预警(条)',
                            dataIndex: 'alarmMiss'
                        }, {
                            title: '网络预警(条)',
                            dataIndex: 'alarmNetwork'
                        }, {
                            title: '挂科预警(条)',
                            dataIndex: 'alarmFail'
                        }, {
                            title: '消费预警(条)',
                            dataIndex: 'alarmConsume'
                        }, {
                            title: '夜归预警(条)',
                            dataIndex: 'alarmBack'
                        }, {
                            title: '逃课预警(条)',
                            dataIndex: 'alarmCourse'
                        }]
                    })
                }
            }
        }
    }

}


