import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import BarChart from './BarChart';
import {postAction} from "../../../../axios";
import MainCard from '../../../../common/components/MainCard';
import {hostUrl} from "../../../../env";
import ReactDOM from "react-dom";


export default class YuJingXueYuan extends Component {

    constructor(props){
        super(props);
        this.state = {
            data:[
                // {
                //     collegeName:"机械工程学院",
                //     "已处理":5,
                //     "未处理":5
                // }
            ],
            tab: '1',
            chartIns: undefined,
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                pageNum:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            columns : [{
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院',
                dataIndex: 'college',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }, {
                title: '已处理（条数）',
                dataIndex: 'isDeal'
            }, {
                title: '未处理（条数）',
                dataIndex: 'notDeal'
            }]
        }
    }

    getData () {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filter;
        let data = {
            year: curYear,
            collegeCode: curCollege,
            majorCode: curMajor,
            grade: curGrade,
            classCode: curClass
        };
        postAction(
            "/bigdata/alarmTrigger/getAlarmStatusByCollegeCode",data
        ).then(
            (res) => {
                if (res.success) {

                    const pageInfo = res.obj;
                    let _data = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {
                            _data.push({
                                name: item.collegeName,
                                "未处理": item.notDeal,
                                "已处理": item.isDeal
                            })

                            _tableData.push({
                                key: index + 1,
                                sortIndex: index + 1,
                                college:item.collegeName,
                                notDeal: item.notDeal,
                                isDeal: item.isDeal
                            })
                        }
                    )

                    this.setState({
                        data: _data,
                        tableData: _tableData,
                        pagination:{
                            ...this.state.pagination,
                            total:pageInfo.length,
                        }
                    })
                } else {
                    this.setState({
                        data:[],
                        tableData:[],
                        pagination:{
                            ...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear, curGrade, curCollege, curMajor, curClass} = this.props.filter;

        window.location.href = `${hostUrl}/bigdata/alarmTrigger/exportAlarm?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'yujing-xls')
            }
        })
    }

    render () {
        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">预警学院处理情况
                    <span className="unit">单位：条</span>
                    <div className="filter-text" style={{top:20,left:0}}>{this.props.filter.curYearName}
                        <Divider type="vertical" />{this.props.filter.curCollegeName}
                        <Divider type="vertical" />{this.props.filter.curMajorName}
                        <Divider type="vertical" />{this.props.filter.curGradeName}
                        <Divider type="vertical" />{this.props.filter.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "预警处理结果"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: 'yujing-xls',
                    filename: '预警处理结果excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                        <BarChart
                            geomType="intervalStack"
                            data={this.state.data}
                            colors={['#f04864','#1890ff']}
                            padding={[5,50,80,50]}
                            onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={this.state.columns}
                                bordered
                                size="middle"
                                style={{height:"350px"}}
                                pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount(){
        this.getData();
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院"){
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    },{
                        title: "学院",
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '已处理（条数）',
                        dataIndex: 'isDeal'
                    }, {
                        title: '未处理（条数）',
                        dataIndex: 'notDeal'
                    }]
                })
            } else {
                this.setState({
                    columns: [{
                        title: '序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    }, {
                        title: "专业",
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '已处理（条数）',
                        dataIndex: 'isDeal'
                    }, {
                        title: '未处理（条数）',
                        dataIndex: 'notDeal'
                    }]
                })

                if (nextProps.filterName.curMajorName !== "全部专业") {
                    this.setState({
                        columns: [{
                            title: '序号',
                            dataIndex: 'sortIndex',
                            width: '10%',
                        }, {
                            title: "班级",
                            dataIndex: 'college',
                            render: (text) => {
                                return (
                                    <span title={text}>{text}</span>
                                )
                            }
                        }, {
                            title: '已处理（条数）',
                            dataIndex: 'isDeal'
                        }, {
                            title: '未处理（条数）',
                            dataIndex: 'notDeal'
                        }]
                    })
                }
            }
        }
    }

}


