import React, { Component } from 'react';
import { Row, Col } from 'antd';
import Filter from './Filter';
import './style.less';
import ChuLiJieGuo from "./ChuLiJieGuo";
import LeiBieFenBu from "./LeiBieFenBu";
import BuTongXueYuan from "./BuTongXueYuan";
import QingKuangFenXi from "./QingKuangFenXi";
import YuJingXueYuan from "./YuJingXueYuan";
import GaoWeiYuJing from "./GaoWeiYuJing";
import JiShiChuLiDaBiaoLv from "./JiShiChuLiDaBiaoLv";

class Overview extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filter: {
                curYear:'',
                curCollege: "",
                curMajor: "",
                curGrade:"",
                curClass:"",
            },
            filterName:{
                curYearName:"",
                curCollegeName:"",
                curMajorName:"",
                curGradeName:"",
                curClassName:"",
            }
        };
        this.refreshData = this.refreshData.bind(this);
    }

    //点击搜索按钮
    refreshData(filter,filterName,isHandle){
        if(isHandle){
            Object.assign(this.state.filter,this.state.filterName)
            return;
        }
        this.setState({filter,filterName},()=>{
            Object.assign(filter,filterName)
        })
    };

    render() {
        return (
            <div className="overview">
                <Filter refreshData={this.refreshData} />
                <div className="content">
                    <Row gutter={20} style={{ marginTop: 20 }}>
                        {/*预警处理结果*/}
                        <Col span={12}>
                            <ChuLiJieGuo filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                        {/*预警类别分布*/}
                        <Col span={12}>
                            <LeiBieFenBu filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                    </Row>
                    <Row>
                        {/*不同学院预警分析*/}
                        <Col span={24} style={{marginTop:"20px"}}>
                            <BuTongXueYuan filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                    </Row>
                    <Row>
                        {/*预警情况分析*/}
                        <Col span={24} style={{marginTop:"20px"}}>
                            <QingKuangFenXi filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                    </Row>
                    <Row>
                        {/*预警学院处理情况*/}
                        <Col span={24} style={{marginTop:"20px"}}>
                            <YuJingXueYuan filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                    </Row>
                    <Row>
                        {/*高危预警及时处理达标率*/}
                        <Col span={24} style={{marginTop:"20px"}}>
                            <GaoWeiYuJing filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                    </Row>
                    <Row>
                        {/*预警及时处理达标率*/}
                        <Col span={24} style={{marginTop:"20px"}}>
                            <JiShiChuLiDaBiaoLv filter={this.state.filter} filterName={this.state.filterName}/>
                        </Col>
                    </Row>
                </div>
            </div>
        )
    }
}
export default Overview;



